import { createTheme } from '@material-ui/core/styles';

export const theme = createTheme({
  
  palette: {
    primary: {
      main: '#FF9913',
    },
    secondary: {
      main: '#a84b03'
    },
    type: 'light'
  },
  overrides: {
    MuiCssBaseline: {
      '@global': {
        '*': {
          'scrollbar-width': 'thin',
        },
        '*::-webkit-scrollbar': {
          width: '4px',
          height: '4px',
        }
      }
    },  
    MuiTooltip: {
      tooltip: {
        fontSize:'medium'
      }
    },
    MuiPaper: {
      root: {
        padding:'0',
      }
    },  
    MuiTextField: {
      root: {
        marginBottom: '8px'
      }
    },
    MuiIconButton: {
      root: {
        padding: '4px'  
      }
    },
    MuiAccordionDetails: {
      root: {
        backgroundColor: '#888',
        padding: 0,
      }
    },
    MuiAccordionSummary: {
      root: {
        backgroundColor: 'transparent',
        //border: '1px solid #222',
        marginBottom: -1,
        minHeight: 0,
        '&$expanded': {
          minHeight: 0,
        },
        width: 'auto !important'
      },
      content: {
        '&$expanded': {
          margin: '12px 0',
        },
      },
      expanded: {},
    },
    MuiSelect: {
      select: {
        color: '',
        backgroundColor: '#585858'
      }
    },
    MuiGridList: {
      root: {
        margin: '0px'
      }
    },  
    MuiTabs: {
      root: {
        minHeight: '0px'
      },
    },
    MuiTab: {
      root: {
        minHeight: '0px'
      },
    },
    MuiTabPanel: {
      root: {
        height: '100%'
      },
    },
    MuiFormControlLabel: {
      root: {
        marginBottom: '0',
        color: '#ccc'
      }
    },
    MuiFormLabel: {
      root: {
        color: '#999'
      }
    },
    MuiCircularProgress: {
      root: { 
        zIndex: 500
      }
    },
    MuiBackdrop: {
      root: {
        backgroundColor: 'rgba(0, 0, 0, 0.9)'
      }
    },
    MuiSlider: {
      root: {
        padding: '10px 0 !important',
        color: '#444'
      },
      thumb: {
        color: 'orange'
      },
      rail: {
        backgroundColor: '#666'
      },
      track: {
        backgroundColor: '#888'
      }
    },
    MuiButton: {
      root: {
        padding: '2px 5px',
        margin: '8px',
      },
      label: {
        fontSize: 'small'
      },
      containedPrimary: {
        backgroundColor: '#888 !important'
      }
    },
    MuiButtonBase: {
      root: {
        width:'fit-content'
      }
    },
    MuiMenuItem: {
      root: {
        color: '#eee'
      }
    },
    MuiDivider: {
      root: {
        borderTop: '1px solid #000',
        margin: '5px 0'
      }
    }
  },
});
