import React from "react"
import ErrorOutlined from '@material-ui/icons/ErrorOutlined';
import { appConfig } from "../../config/appConfig";

export default class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);

    this.state = { hasError: false };
  }

  static getDerivedStateFromError() {
    return { hasError: true };
  }

  componentDidCatch(error) {
    if (!appConfig.release)
      console.log(error);
  }

  render() {
    if (this.state.hasError) {
      return (
        <div className='std-page center-page-x center'>
          <div className='error-box'>
            <ErrorOutlined className='error-icon' />
            <h2>Oops</h2>
            Something went wrong, please check your internet connection and try again. <br />
            If this error persists contact support.
          </div>
        </div>
      );
    }

    return this.props.children;
  }
}