import React, { Fragment } from 'react';
import { Switch, Route, BrowserRouter, Redirect } from 'react-router-dom';
import ErrorBoundary from './common/error/errorBoundary';
import { ThemeProvider } from '@material-ui/core/styles';
import { theme } from './theme.js';

import './App.scss';
import './App.css'
import WhatIsExtendedRealityBlog from './public/blog/whatIsExtendedReality';
//import FeedbackForm from 'main/forms/feedbackForm';
////import PanoramaViewer from 'main/panoramaViewer';

// public
const HomeAppliances = React.lazy(() => import('./public/platform/homeAppliances'));
const Fashion = React.lazy(() => import('./public/platform/fashion'));
const Jewellery = React.lazy(() => import('./public/platform/jewellery'));
const Furniture = React.lazy(() => import('./public/platform/funiture'));
const WebViewer = React.lazy(() => import('./public/platform/webViewer'));
const Panorama360Viewer = React.lazy(() => import('./public/platform/panorama360Viewer'));
const AugmentedReality = React.lazy(() => import('./public/platform/augmentedReality'));
const VirtualReality = React.lazy(() => import('./public/platform/virtualReality'));

const Configurator = React.lazy(() => import('./public/platform/configurator'));
const About = React.lazy(() => import('./public/about'));
const Faq = React.lazy(() => import('./public/faq'));
const ProductModelling = React.lazy(() => import('./public/productModelling'));

const Demos = React.lazy(() => import('./public/demos/demos'));

const Embed = React.lazy(() => import('./members/embed'));
const Header = React.lazy(() => import('./common/layout/header'));
const Footer = React.lazy(() => import('./common/layout/footer'));
const TestPage = React.lazy(() => import('./public/testPage'));
const PageNotFound = React.lazy(() => import('./common/error/pageNotFound'));
const LandingPage = React.lazy(() => import('./public/landingPage'));
const Plans = React.lazy(() => import('./common/plans/plans'));
const EzyvueStudio = React.lazy(() => import('./members/features/ezyvueStudio/ezyvueStudio'));
const ModelViewer = React.lazy(() => import('./public/modelViewer'));
const UserHome = React.lazy(() => import('./members/home'));
const TermsOfService = React.lazy(() => import('./public/termsOfService'));
const PrivacyPolicy = React.lazy(() => import('./public/privacyPolicy'));
const ContactPage = React.lazy(() => import('./public/contactPage'));
const Help = React.lazy(() => import('./members/help/index'));
const Uploading = React.lazy(() => import('./members/help/uploading'));
const Editing = React.lazy(() => import('./members/help/editing'));
const SceneOptions = React.lazy(() => import('./members/help/sceneOptions'));
const Options = React.lazy(() => import('./members/help/options'));
const Animations = React.lazy(() => import('./members/help/animations'));
const LightingOptions = React.lazy(() => import('./members/help/lightingOptions'));
const EditMaterials = React.lazy(() => import('./members/help/editMaterials'));
const ProjectDetails = React.lazy(() => import('./members/help/projectDetails'));
const Annotations = React.lazy(() => import('./members/help/annotations'));
const ProductConfiguratorHelp = React.lazy(() => import('./members/help/productConfigurator'));
const Effects = React.lazy(() => import('./members/help/effects'));
const ViewerHelp = React.lazy(() => import('./members/help/viewer'));
const CameraOptions = React.lazy(() => import('./members/help/cameraOptions'));
const Audio = React.lazy(() => import('./members/help/audio'));
const EmbedViewerOnWix = React.lazy(() => import('./members/help/embedViewerOnWix'));
const EmbedViewerOnShopify = React.lazy(() => import('./members/help/embedViewerOnShopify'));
const EmbedViewerOnSquarespace = React.lazy(() => import('./members/help/embedViewerOnSquarespace'));
const EmbedViewerOnWebflow = React.lazy(() => import('./members/help/embedViewerOnWebflow'));
const EmbedViewerOnWordPress = React.lazy(() => import('./members/help/embedViewerOnWordpress'));
const EmbedViewer = React.lazy(() => import('./members/help/embedViewer'));
const CustomerFeedback = React.lazy(() => import('./public/customerFeedback'));
const QRCodeGenerator = React.lazy(() => import('./members/qrCodeGenerator'));
const ManagePanoramas = React.lazy(() => import('./members/features/panoramas/managePanoramas'));

const Blog = React.lazy(() => import('./public/blog/blog'));
const ArBenefitsInECommerceBlog = React.lazy(() => import('./public/blog/arBenefitsInECommerce'));
const AdvantagesOf3DViewerBlog = React.lazy(() => import('./public/blog/advantagesOf3DViewer'));
const TenUsesOfAugmentedReality = React.lazy(() => import('./public/blog/tenUsesOfAugmentedReality'));
const Add3dViewerToWebsiteBlog = React.lazy(() => import('./public/blog/add3dViewerToWebsite'));
const Add3DViewerToWixBlog = React.lazy(() => import('./public/blog/add3dViewerToWix'));
const Add3DViewerToShopifyBlog = React.lazy(() => import('./public/blog/add3dProductViewerToShopify'));
const DisplayProductsIn3DAndAROnGoDaddyBlog = React.lazy(() => import('./public/blog/displayProductsIn3DAndAROnGoDaddy'));

class App extends React.Component {
  renderChildren() {
    return (
      <Fragment>
        <Header />
        <ErrorBoundary>
          <Switch>
            <Route exact path='/home' component={UserHome}></Route>
            <Route exact path='/embed' component={Embed}></Route>
            <Route exact path='/editor' component={EzyvueStudio}></Route>
            <Route exact path='/help' component={Help}></Route>
            <Route exact path='/help/animations' component={Animations}></Route>
            <Route exact path='/help/uploading' component={Uploading}></Route>
            <Route exact path='/help/editing' component={Editing}></Route>
            <Route exact path='/help/sceneOptions' component={SceneOptions}></Route>
            <Route exact path='/help/options' component={Options}></Route>
            <Route exact path='/help/lightingOptions' component={LightingOptions}></Route>
            <Route exact path='/help/editMaterials' component={EditMaterials}></Route>
            <Route exact path='/help/annotations' component={Annotations}></Route>
            <Route exact path='/help/projectDetails' component={ProjectDetails}></Route>
            <Route exact path='/help/effects' component={Effects}></Route>
            <Route exact path='/help/viewer' component={ViewerHelp}></Route>
            <Route exact path='/help/productConfigurator' component={ProductConfiguratorHelp}></Route>
            <Route exact path='/help/audio' component={Audio}></Route>
            <Route exact path='/help/cameraOptions' component={CameraOptions}></Route>
            <Route exact path='/help/embedViewerOnShopify' component={EmbedViewerOnShopify}></Route>
            <Route exact path='/help/embedViewerOnWix' component={EmbedViewerOnWix}></Route>
            <Route exact path='/help/embedViewerOnSquarespace' component={EmbedViewerOnSquarespace}></Route>
            <Route exact path='/help/embedViewerOnWebflow' component={EmbedViewerOnWebflow}></Route>
            <Route exact path='/help/embedViewerOnWordPress' component={EmbedViewerOnWordPress}></Route>
            <Route exact path='/help/embedViewer' component={EmbedViewer}></Route>
            <Route exact path='/customerFeedback' component={CustomerFeedback}></Route>
            <Route exact path='/qrCodeGenerator' component={QRCodeGenerator}></Route>
            <Route exact path='/panoramas/managePanoramas' component={ManagePanoramas}></Route>

            <Route exact path='/' component={LandingPage} />
            <Route exact path='/landingpage' component={LandingPage} />
            <Route exact path='/faq' component={Faq}></Route>
            <Route exact path='/demos' component={Demos}></Route>
            <Route exact path='/pricing' component={Plans}></Route>
            <Route exact path='/termsOfService' component={TermsOfService}></Route>
            <Route exact path='/privacyPolicy' component={PrivacyPolicy}></Route>
            <Route exact path='/about' component={About}></Route>
            <Route exact path='/contact' component={ContactPage}></Route>
            <Route exact path='/productModelling' component={ProductModelling}></Route>
            <Route exact path='/test' component={TestPage}></Route>
            <Route exact path='/modelViewer' component={ModelViewer}></Route>
            {/*                 <Route exact path='/panoramaViewer' component={PanoramaViewer}></Route> */}
            <Route exact path='/platform/configurator' component={Configurator}></Route>
            <Route exact path='/platform/webviewer' component={WebViewer}></Route>
            <Route exact path='/platform/360panoramaviewer' component={Panorama360Viewer}></Route>
            <Route exact path='/platform/augmentedreality' component={AugmentedReality}></Route>
            <Route exact path='/platform/vr' component={VirtualReality}></Route>
            <Route exact path='/industries/homeAppliances' component={HomeAppliances}></Route>
            <Route exact path='/industries/fashion' component={Fashion}></Route>
            <Route exact path='/industries/jewellery' component={Jewellery}></Route>
            <Route exact path='/industries/furniture' component={Furniture}></Route>
            {/*  <Route exact path='/forms/feedbackform' component={FeedbackForm}></Route> */}

            <Route exact path='/blog/benefitsOfArinEcommerce' component={ArBenefitsInECommerceBlog}></Route>
            <Route exact path='/blog/enhanceWebsiteWithA3DViewer' component={AdvantagesOf3DViewerBlog}></Route>
            <Route exact path='/blog/tenUsesOfAugmentedReality' component={TenUsesOfAugmentedReality}></Route>
            <Route exact path='/blog/addingA3dViewerToAWebsite' component={Add3dViewerToWebsiteBlog}></Route>
            <Route exact path='/blog/add3DViewerToShopify' component={Add3DViewerToShopifyBlog}></Route>
            <Route exact path='/blog/add3DViewerToWix' component={Add3DViewerToWixBlog}></Route>
            <Route exact path='/blog/whatIsExtendedReality' component={WhatIsExtendedRealityBlog}></Route>
            <Route exact path='/blog/displayProductsIn3DAndAROnGoDaddy' component={DisplayProductsIn3DAndAROnGoDaddyBlog}></Route>
            <Route exact path='/blog/blog' component={Blog}></Route>

            <Route path='*' component={PageNotFound} />
          </Switch>
        </ErrorBoundary>
        <Footer />
      </Fragment>
    )
  }

  render() {
   // const hasChildNodes = document.getElementById("root").hasChildNodes();

    return (
      <div id='site-root'>
        <ThemeProvider theme={theme}>
          <BrowserRouter>
            {
           /*    hasChildNodes ?
                this.renderChildren()
                : */
                <React.Suspense fallback={<div />}>
                  {this.renderChildren()}
                </React.Suspense>
            }
          </BrowserRouter>
        </ThemeProvider>
      </div>
    )
  }
}

export default App;